'use client';

import { useState } from 'react';
import Image from 'next/image';

type Skill = {
  id: number;
  name: string;
  color: string;
};

type LearningInterest = {
  id: number;
  name: string;
};

const ProfileCard = () => {
  const [skills] = useState<Skill[]>([
    { id: 1, name: 'Dance', color: 'bg-cyan-800/60' },
    { id: 2, name: 'Dance Flows', color: 'bg-cyan-800/60' },
  ]);

  const [learningInterests] = useState<LearningInterest[]>([
    { id: 1, name: 'Web Development' },
    { id: 2, name: 'Branding' },
  ]);

  const [isLiked, setIsLiked] = useState(false);
  const [isMessaged, setIsMessaged] = useState(false);
  const [isSuperLiked, setIsSuperLiked] = useState(false);

  const handleAction = (action: 'like' | 'message' | 'superLike') => {
    switch (action) {
      case 'like':
        setIsLiked(!isLiked);
        // Add your like logic here
        break;
      case 'message':
        setIsMessaged(!isMessaged);
        // Add your message logic here
        break;
      case 'superLike':
        setIsSuperLiked(!isSuperLiked);
        // Add your super like logic here
        break;
    }
  };

  return (
    <div className="w-96 h-[844px] relative bg-gray-50 overflow-hidden font-['Inter']">
      {/* Header */}
      <div className="w-80 h-16 left-[36px] top-[36px] absolute">
        <div className="left-[64px] top-[31px] absolute text-blue-950 text-sm font-semibold">
          Halo, user!
        </div>
        <div className="w-14 h-14 left-0 top-[1px] absolute rounded-full overflow-hidden">
          <Image
            src="https://placehold.co/56x56"
            alt="Profile"
            width={56}
            height={56}
            className="object-cover w-full h-full"
          />
        </div>
        <div className="right-0 top-[30px] absolute flex items-center gap-1">
          <span className="text-amber-400 text-base font-normal">Level 1</span>
          <div className="w-2.5 h-2.5 bg-amber-300 rounded-full" />
        </div>
        <div className="right-0 top-[47px] absolute text-blue-950 text-xs font-normal">
          0 matches
        </div>
      </div>

      {/* Main Content */}
      <div className="w-80 h-[647px] left-[31px] top-[115px] absolute">
        {/* Profile Image with Blurred Background */}
        <div className="relative">
          <div className="w-80 h-32 rounded-[10px] blur-[50px] overflow-hidden">
            <Image
              src="https://placehold.co/324x132"
              alt="Profile background"
              width={324}
              height={132}
              className="w-full h-full object-cover"
            />
          </div>
          <div className="absolute left-1/2 transform -translate-x-1/2 -bottom-20 w-40 h-40 rounded-full border-2 border-gray-50 overflow-hidden">
            <Image
              src="https://placehold.co/156x156"
              alt="Profile"
              width={156}
              height={156}
              className="w-full h-full object-cover"
            />
          </div>
        </div>

        {/* Rating */}
        <div className="flex items-center justify-center mt-24 mb-2">
          <div className="flex items-center bg-white rounded-[35px] px-3 py-1.5">
            <span className="text-yellow-500 mr-1">‚òÖ</span>
            <span className="text-black text-base font-medium">4.9</span>
          </div>
        </div>

        {/* User Info */}
        <div className="text-center mt-2">
          <h1 className="text-slate-950 text-2xl font-bold">User Test, 21</h1>
          <p className="text-cyan-800 text-base">Ia»ôi, Rom√¢nia</p>
        </div>

        {/* Bio */}
        <p className="mt-4 text-slate-950 text-sm text-center px-4">
          I am passionate about dance and have developed strong rhythm, coordination, and body control through consistent practice. Dancing allows me to express creativity and confidence.
        </p>

        {/* Skills */}
        <div className="mt-6 px-4">
          <h2 className="text-slate-950 text-xl font-medium mb-2">Skills:</h2>
          <div className="flex flex-wrap gap-2">
            {skills.map((skill) => (
              <span
                key={skill.id}
                className={`${skill.color} text-white text-base font-medium px-3 py-1.5 rounded-[10px]`}
              >
                {skill.name}
              </span>
            ))}
          </div>
        </div>

        {/* Looking to Learn */}
        <div className="mt-6 px-4">
          <h2 className="text-slate-950 text-xl font-medium mb-2">Looking to learn:</h2>
          <div className="flex flex-wrap gap-2">
            {learningInterests.map((interest) => (
              <span
                key={interest.id}
                className="bg-blue-950 text-white text-base font-medium px-3 py-1.5 rounded-[10px]"
              >
                {interest.name}
              </span>
            ))}
          </div>
        </div>

        {/* Action Buttons */}
        <div className="absolute bottom-4 left-0 right-0 flex justify-center gap-8">
          <button
            onClick={() => handleAction('like')}
            className={`w-16 h-16 rounded-full flex items-center justify-center ${
              isLiked ? 'bg-green-500' : 'bg-gray-200'
            }`}
          >
            {isLiked ? '‚ù§Ô∏è' : 'üëç'}
          </button>
          
          <button
            onClick={() => handleAction('message')}
            className={`w-16 h-16 rounded-full flex items-center justify-center ${
              isMessaged ? 'bg-blue-500' : 'bg-gray-200'
            }`}
          >
            üí¨
          </button>
          
          <button
            onClick={() => handleAction('superLike')}
            className={`w-16 h-16 rounded-full flex items-center justify-center ${
              isSuperLiked ? 'bg-purple-500' : 'bg-gray-200'
            }`}
          >
            ‚≠ê
          </button>
        </div>
      </div>

      {/* Bottom Navigation */}
      <div className="w-full h-16 absolute bottom-0 bg-gray-50 shadow-[4px_0px_1px_0px_rgba(29,50,78,1.00)] outline outline-[0.50px] outline-slate-400 flex justify-around items-center">
        <button className="flex flex-col items-center">
          <div className="w-6 h-6 border border-blue-950"></div>
          <span className="text-slate-950 text-[10px]">Discover</span>
        </button>
        <button className="flex flex-col items-center">
          <div className="w-6 h-6 border border-blue-950"></div>
          <span className="text-slate-950 text-[10px]">Filter</span>
        </button>
        <button className="flex flex-col items-center">
          <div className="w-6 h-6 border border-blue-950"></div>
          <span className="text-slate-950 text-[10px]">Chat</span>
        </button>
        <button className="flex flex-col items-center">
          <div className="w-6 h-6 border border-blue-950"></div>
          <span className="text-slate-950 text-[10px]">Notify</span>
        </button>
        <button className="flex flex-col items-center">
          <div className="w-6 h-6 border border-blue-950"></div>
          <span className="text-slate-950 text-[10px]">Profile</span>
        </button>
      </div>
    </div>
  );
};

export default ProfileCard;

